
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
components:{
XHeader,InlineLoading
},
data(){
return {
login:false
}
},
methods:{
starActivity(index) {
if(this.$root.loginInfo.isLogin==true){
if(index==1){//安全中心
this.$router.push('safecenter')
}else if(index==2){//修改手机号码
this.$router.push('/changephonenumber')
}
}else{
if(this.$root.phoneType=='ios'){
window.webkit.messageHandlers.mutual.postMessage({type: "1"});
}else{
android:AndroidFun.mutual('1');
}
}

},
mutual(type){// 1登陆 3注册 4退出登陆 5清除缓存 6检查更新 8去评分
if(this.$root.phoneType=='ios'){
window.webkit.messageHandlers.mutual.postMessage({type: type});
}else{
android:AndroidFun.mutual(type);
}
if (type == '1') {
this.$router.isBack = true;
this.$router.push('/mine');
}
if(type=='4'){
this.$root.loginInfo.isLogin = false;
this.$router.isBack = true;
this.$router.push('/mine');
}
}
}
}



